:root {
  --white-basic: #ffffff;
  --green-basic: #68b738;
  --green-semi-dark: #5eaa2f;
  --pink-basic: #ff8282;
  --grey-extra-light: #f2f2f2;
  --grey-light: #eaeaea;
  --grey-light-lighter: #e7e7e7;
  --grey-light-darker: #e6e6e6;
  --grey-semi-light: #ebebeb;
  --grey-gainsboro: #dcdcdc;
  --grey-basic-light: #d9d9d9;
  --grey-basic: #cdcdcd;
  --grey-grey73: #bababa;
  --grey-semi-dark: #666666;
  --grey-dark: #444444;
  --grey-extra-dark: #222222;
  --black-light: #111111;
  --black-basic: #000000;
  --black-transparent-16: rgba(0, 0, 0, 0.16);
  --black-transparent-19: rgba(0, 0, 0, 0.19);
  --black-transparent-20: rgba(0, 0, 0, 0.2);
  --black-transparent-24: rgba(0, 0, 0, 0.24);
  --black-transparent-25: rgba(0, 0, 0, 0.25);
  --black-transparent-30: rgba(0, 0, 0, 0.3);
  --black-transparent-60: rgba(0, 0, 0, 0.6);
  --grey-transparent-30: rgba(68, 68, 68, 0.3);
  --white-transparent-30: rgba(255, 255, 255, 0.3);
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local(""), url("../fonts/oswaldregular.woff2") format("woff2"), /* Super Modern Browsers */ url("../fonts/oswaldregular.woff") format("woff");
  /* Modern Browsers */
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: local(""), url("../fonts/oswaldmedium.woff2") format("woff2"), /* Super Modern Browsers */ url("../fonts/oswaldmedium.woff") format("woff");
