  .page-header--form {
    padding-bottom: 73px;
  }
}
@media (max-width: 767px) {
  .page-header--closed {
    padding-bottom: 12px;
  }
}
@media (max-width: 767px) {
  .page-header--open {
    border-bottom: 1px solid var(--grey-light-darker);
    box-shadow: 0 4px 4px var(--black-transparent-25);
  }
}
.page-header__logo-picture-text {
  justify-self: center;
}
.page-header__navigation-toggle--closed {
  position: relative;
  align-self: center;
  justify-self: end;
  width: 24px;
  height: 1.5px;
  background-color: var(--black-basic);
  border: none;
}
@media (min-width: 768px) {
  .page-header__navigation-toggle--closed {
    display: none;
  }
}
@media (min-width: 1200px) {
  .page-header__navigation-toggle--closed {
    display: none;
  }
}
.page-header__navigation-toggle--closed::before {
  content: "";
  position: absolute;
  top: -10px;
  left: 0;
  width: 24px;
  height: 1.5px;
  background-color: var(--black-basic);
}
.page-header__navigation-toggle--closed::after {
  content: "";
  position: absolute;
  top: 10px;
  left: 0;
  width: 24px;
  height: 1.5px;
  background-color: var(--black-basic);
}
.page-header__navigation-toggle--open {
  position: relative;
  margin-left: auto;
  margin-bottom: 51px;
  background-color: transparent;
  border: none;
}
@media (min-width: 768px) {
  .page-header__navigation-toggle--open {
    display: none;
  }
}
@media (min-width: 1200px) {
  .page-header__navigation-toggle--open {
    display: none;
  }
}
.page-header__navigation-toggle--open::before {
  content: "";
  position: absolute;
  top: 18px;
  left: -12px;
  width: 26px;
  height: 1.5px;
  background-color: var(--black-basic);
  transform: rotate(45deg);
}
.page-header__navigation-toggle--open::after {
  content: "";
  position: absolute;
  top: 18px;
  left: -13px;
  width: 26px;
  height: 1.5px;
  background-color: var(--black-basic);
  transform: rotate(-45deg);
}
.page-header__main-navigation {
  grid-column-start: 1;
  grid-column-end: 3;
}
@media (min-width: 768px) {
  .page-header__main-navigation {
    grid-column-start: 2;
    grid-column-end: 3;
  }
}
@media (min-width: 1200px) {
  .page-header__main-navigation {
