.page-footer {
  background-image: linear-gradient(to bottom, transparent 64%, var(--grey-light) 64%);

  @media (min-width: @tablet-width) {
    background-image: linear-gradient(to bottom, transparent 78%, var(--grey-light) 78%);
  }

  @media (min-width: @desktop-width) {
    background-image: linear-gradient(to bottom, transparent 70%, var(--grey-light) 70%);
  }
}

.page-footer__info-wrapper {
  display: flex;
  flex-direction: column;

  padding-top: 38px;
  padding-bottom: 22px;

  @media (min-width: @tablet-width) {
    flex-direction: row;
    justify-content: space-between;
    align-items: baseline;

    padding-top: 53px;
    padding-bottom: 61px;
  }

  @media (min-width: @desktop-width) {
    padding-top: 55px;
  }
}

.page-footer__logo-text {
  @media (max-width: @mobile-width-max) {
    padding-right: 76px;
    padding-bottom: 29px;
    padding-left: 76px;

    border-bottom: 1px solid var(--grey-basic-light);
  }

  @media (min-width: @tablet-width) {
    align-self: center;

    padding-right: 17px;
  }

  &:hover[href],
  &:focus[href] {
    opacity: 0.8;
  }

  &:active[href] {
    opacity: 0.6;
  }
}
